.user-details {
    margin-bottom: 44px;
    padding-top: 40px;
    padding-bottom: 20px;
    margin: 0 auto;
    text-align: center;
}

.user-avatar-circle {
    width: 120px;
    height: 120px;
    border-radius: 60px;
    line-height: 60px;
}

.user-avatar-circle > * {
    line-height: 120px;
    font-size: 40px;
    position: relative !important;
    left: 0 !important;
}

.user-summary {
    text-align: center;
    padding-top: 20px;
}

@media (min-width: 576px) {
    .user-details {
        text-align: left;
        display: table;
    }    

    .user-avatar, .user-summary {
        float: left;
        text-align: left;
    }

    .user-summary {
        padding-top: 0;
        padding-left: 40px;
        width: calc(100% - 120px);
    }
}

.user-summary .full-name {
    font-size: 30px;
    font-weight: 600;
    color: rgba(0,0,0,0.85);
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.user-summary .username {
    font-size: 20px;
}

.user-summary .user-joined {
    margin-top: 5px;
}

@media (max-width: 768px) {
    .user-summary .full-name {
        font-size: 24px;
    }    
}


/**File Css **/


.files-dropzone-list {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    padding: 10px;
    border: 1px dashed #D3D3D3;
    cursor: pointer;
  }
  
  .files-buttons {
    border-top: 1px solid #D3D3D3;
    cursor: pointer;
  }
  .files-button-submit {
    display: inline-block;
    text-align: center;
    height: 40px;
    line-height: 40px;
    width: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-right: 1px solid #D3D3D3;
  }
  .files-button-submit:before {
    content: "Submit"
  }
  .files-button-clear {
    display: inline-block;
    text-align: center;
    height: 40px;
    line-height: 40px;
    width: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  .files-button-clear:before {
    content: "Clear"
  }
  .files-list {
    width: 300px;
  }
  .files-list ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .files-list li:last-child {
    border: none;
  }
  .files-list-item {
    height: 60px;
    padding: 10px 0px 10px 10px;
    /*border-bottom: 1px solid #D3D3D3;*/
  }
  .files-list-item-content {
    float: left;
    padding-top: 5px;
    padding-left: 10px;
    width: calc(100% - 130px);
  }
  .files-list-item-content-item {
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
  }
  .files-list-item-content-item-1 {
    font-size: 20px;
    line-height: 30px;
  }
  .files-list-item-content-item-2 {
    font-size: 16px;
    line-height: 20px;
  }
  .files-list-item-preview {
    height: 60px;
    width: 60px;
    float: left;
  }
  .files-list-item-preview-image {
    height: 100%;
    width: 100%;
  }
  .files-list-item-preview-extension {
    text-align: center;
    line-height: 60px;
    color: #FFF;
    background-color: #D3D3D3;
    font-size: 16px;
    overflow: hidden;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding-left: 5px;
    padding-right: 5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  .files-list-item-remove {
    height: 60px;
    width: 60px;
    float: right;
    cursor: pointer;
    background: url(data:image/svg+xml;base64,) no-repeat center center;
    background-size: 30px 30px;
  }
  .files-list-item-remove-image {
    height: 30px;
    width: 30px;
    margin-top: 15px;
    margin-right: 10px;
    float: right;
  }
  
  
  
  /* GALLERY */
  /* ================== */
  
  .files-dropzone-gallery {
    padding: 10px;
    border: 1px dashed #D3D3D3;
    min-height: 300px;
    width: 500px;
  }
  .files-gallery-item {
    height: 80px;
    margin: 5px;
  }
  
  
  .files-dropzone-active {
    border: 1px solid lightgreen;
    color: lightgreen;
  }

  .files-list{
      width: -webkit-fill-available;
  }



